<script setup>
import { defineProps, computed } from 'vue'

const props = defineProps({
  posts: {
    type: Array,
    default: () => []
  },
  selectedCategories: {
    type: Array,
    default: () => []
  }
})

// æ ¹æ®é€‰ä¸­çš„åˆ†ç±»è¿‡æ»¤æ–‡ç« 
const filteredPosts = computed(() => {
  if (!props.selectedCategories || props.selectedCategories.length === 0) {
    return props.posts
  }
  return props.posts.filter(post => props.selectedCategories.includes(post.category))
})

// ç‚¹å‡»æ–‡ç« æ ‡é¢˜æˆ–é˜…è¯»å…¨æ–‡æ—¶ï¼Œè§¦å‘äº‹ä»¶å‘Šè¯‰çˆ¶ç»„ä»¶â€œæŸ¥çœ‹æ–‡ç« è¯¦æƒ…â€
const viewPost = (slug) => {
  // è§¦å‘è‡ªå®šä¹‰äº‹ä»¶ï¼Œä¼ é€’æ–‡ç« çš„ slug æˆ– id
  // è¿™ä¸ªäº‹ä»¶éœ€è¦çˆ¶ç»„ä»¶ç›‘å¬ @view-post="handleViewPost"
  // è¿™æ ·çˆ¶ç»„ä»¶å°±èƒ½çŸ¥é“ç”¨æˆ·ç‚¹å‡»äº†å“ªç¯‡æ–‡ç« 
  emit('view-post', slug)
}

const emit = defineEmits(['view-post'])
</script>

<template>
  <div class="posts">
    <h2>æœ€æ–°æ–‡ç« </h2>
    <!-- éå†ç­›é€‰åçš„æ–‡ç«  -->
    <article v-for="(post, index) in filteredPosts" :key="index" class="post-card" >
      <div class="image-container">
        <img :src="post.image" alt="Post image" class="post-image" />
      </div>
      <div class="content-container">
        <div class="title-date-container">
          <!-- ç”¨æ™®é€šå…ƒç´ æ›¿ä»£ router-linkï¼Œç‚¹å‡»è§¦å‘äº‹ä»¶ -->
          <h3 class="title" @click="viewPost(post.slug)">
            {{ post.title }}
          </h3>
          <p class="date">{{ post.date }}</p>
        </div>
        <p class="summary">{{ post.summary }}</p>
        <div class="last-container">
          <div class="category">
            <span class="category-label">åˆ†ç±»: </span>
            <span class="category-name">{{ post.category }}</span>
          </div>
          <a href="javascript:;" class="read-more" @click.prevent="viewPost(post.slug)">
            é˜…è¯»å…¨æ–‡ â†’
          </a>
        </div>
      </div>
    </article>
  </div>
</template>


