<script setup>
import {ref, onMounted, onBeforeUnmount, computed} from 'vue';
import Header from '@/components/Header.vue';
import {ArrowUp} from '@element-plus/icons-vue';
// import PostList from "@/components/PostList.vue";
import Sidebar from "@/components/Sidebar.vue";

import { useRouter } from 'vue-router';

import PostList from '../components/PostList.vue'
import PostDetail from '../components/PostDetail.vue'

// æ§åˆ¶æ˜¾ç¤ºå“ªä¸ªç»„ä»¶
const currentComponent = ref(PostList)
const selectedPostId = ref(null)




const router = useRouter();
// çˆ¶ç»„ä»¶çŠ¶æ€ï¼šå½“å‰é€‰ä¸­çš„åˆ†ç±»
const selectedCategories = ref([]);

// è·å–ç”¨æˆ·ä¿¡æ¯å’Œæ–‡ç« æ•°æ®
const username = 'æœæ±';
const bio = 'ä¸‡ç‰©çš†åœ¨ä¸‡ç‰©ä¹‹ä¸­';
const school = 'ä¸­å—å¤§å­¦';
const location = 'æ¹–å—';

const avatar = require('@/assets/avatar.png');
const coverImage = require('@/assets/cover.jpg');
const education = {
  school: 'ä¸­å—å¤§å­¦',
  degree: 'æœ¬ç§‘',
  major: 'è®¡ç®—æœºç§‘å­¦ä¸æŠ€æœ¯',
  graduationYear: 2026,
  icon: require('@/assets/csu.png')
};

// å­˜å‚¨æ–‡ç« æ•°æ®
const posts = ref([]);



// ä½¿ç”¨ require.context æ¥åŠ¨æ€åŠ è½½ Markdown æ–‡ä»¶
const requireArticles = require.context('@/articles', false, /\.md$/);

// åŠ¨æ€åŠ è½½æ‰€æœ‰æ–‡ç« 
const loadArticles = async () => {
  const articles = requireArticles.keys().map((file) => {
    const module = requireArticles(file);

    const { title, date, category, summary, image } = module.attributes || {};
    const content = module.html || '';

    // ä½¿ç”¨æ ‡é¢˜ç”Ÿæˆä¸€ä¸ª slug
    const slug = title
        .toLowerCase()
        .replace(/\s+/g, '-')  // æ›¿æ¢ç©ºæ ¼ä¸º-
        .replace(/[^\w-]/g, '') // å»æ‰éå­—æ¯æ•°å­—å­—ç¬¦
        .replace(/-$/, '');     // å»æ‰æœ«å°¾çš„çŸ­æ¨ªçº¿

    let imagePath = require(`@/assets/${image}`);

    return {
      slug,       // ç”Ÿæˆ slug
      title,
      date,
      category,
      summary,
      content,
      image: imagePath,
    };
  });

  posts.value = articles;
};


// æ§åˆ¶è¿”å›é¡¶éƒ¨æŒ‰é’®æ˜¾ç¤º
const showBackToTop = ref(false);

// æ»šåŠ¨äº‹ä»¶ç›‘å¬
const handleScroll = () => {
  showBackToTop.value = window.scrollY > 300; // æ»šåŠ¨è¶…è¿‡300pxæ—¶æ˜¾ç¤ºæŒ‰é’®
};

// æ»šåŠ¨åˆ°é¡¶éƒ¨çš„å‡½æ•°
const scrollToTop = () => {
  window.scrollTo({top: 0, behavior: 'smooth'}); // å¹³æ»‘æ»šåŠ¨åˆ°é¡¶éƒ¨
};

// é¡µé¢æŒ‚è½½æ—¶ç›‘å¬æ»šåŠ¨äº‹ä»¶
onMounted(() => {
  loadArticles(); // åŠ è½½æ–‡ç« 
  window.addEventListener('scroll', handleScroll);
});

// é¡µé¢å¸è½½æ—¶ç§»é™¤æ»šåŠ¨äº‹ä»¶ç›‘å¬
onBeforeUnmount(() => {
  window.removeEventListener('scroll', handleScroll);
});

// é€šè¿‡è®¡ç®—å±æ€§è·å–å”¯ä¸€çš„åˆ†ç±»
const categories = computed(() => {
  const allCategories = posts.value.map(post => post.category);
  return [...new Set(allCategories)];  // ä½¿ç”¨ Set å»é‡
});

// eslint-disable-next-line no-unused-vars
const navigateToPosts = () => {
  router.currentRoute.value.meta.posts = posts.value;
  router.currentRoute.value.meta.selectedCategories = selectedCategories.value;

  router.push({ name: 'PostList' });
};


// æ–‡ç« åˆ—è¡¨ä¸­ç‚¹å‡»æŸä¸€é¡¹
const handleViewPost = (postId) => {
  selectedPostId.value = postId
  currentComponent.value = PostDetail
}

// ä»è¯¦æƒ…è¿”å›
const handleBack = () => {
  currentComponent.value = PostList
  selectedPostId.value = null
}

</script>

<template>
  <div class="page">
    <!-- é¡¶éƒ¨å¯¼èˆª -->
    <div class="headers">
      <Header
          :username="username"
          :bio="bio"
          :school="school"
          :location="location"
          :avatar="avatar"
          :cover-image="coverImage"
          :education="education"
      />
    </div>

    <!-- ä¸»ä½“å†…å®¹ -->
    <main class="container main-content">
      <!-- ä¼ é€’é€‰ä¸­çš„åˆ†ç±»åˆ° PostList ç»„ä»¶ -->
<!--      <div class="nav-window" @click="navigateToPosts">-->
<!--        <router-view />-->
<!--      </div>-->

      <component
          :is="currentComponent"
          v-model:selectedCategories="selectedCategories"
          :slug="selectedPostId"
          :posts="posts"
          :categories="categories"
          @view-post="handleViewPost"
          @back="handleBack"
      />

      <!-- ä¼ é€’ categories ç»™ Sidebarï¼Œç›‘å¬åˆ†ç±»é€‰æ‹© -->
      <Sidebar
          :categories="categories"
          v-model:selectedCategories="selectedCategories"
      />
    </main>

    <!-- è¿”å›é¡¶éƒ¨æŒ‰é’® -->
    <div v-if="showBackToTop" @click="scrollToTop" class="back-to-top">
      <el-icon class="el-icon">
        <ArrowUp class="el-icons"/>
      </el-icon>
    </div>

    <!-- é¡µè„š -->
    <footer class="footer">
      <p>&copy; 2025 MyBlog. ç”± Vue 3 é©±åŠ¨ã€‚</p>
    </footer>
  </div>
</template>

