<script setup>
import { defineProps, ref, watch, defineEmits } from 'vue';

const props = defineProps({
  selectedCategories: {
    type: Array,
    default: () => [] // ä¿è¯ä¼ å…¥çš„æ˜¯æ•°ç»„ï¼Œé¿å… undefined
  },
  categories: {
    type: Array,
    default: () => [] // ä¿è¯åˆ†ç±»ä¹Ÿæ˜¯æ•°ç»„
  }
});

// å®šä¹‰ emit äº‹ä»¶

// eslint-disable-next-line vue/valid-define-emits
const emit = defineEmits();

// å°†é€‰ä¸­çš„åˆ†ç±»ä¿å­˜åœ¨æœ¬åœ°å“åº”å¼å˜é‡ä¸­
const selectedCategories = ref([...props.selectedCategories]);

// åˆ‡æ¢åˆ†ç±»
const toggleCategory = (category) => {
  if (selectedCategories.value.includes(category)) {
    selectedCategories.value = selectedCategories.value.filter(item => item !== category);
  } else {
    selectedCategories.value.push(category);
  }

  // è§¦å‘çˆ¶ç»„ä»¶æ›´æ–°é€‰ä¸­åˆ†ç±»
  emit('update:selectedCategories', selectedCategories.value);
};

// è§‚å¯Ÿ selectedCategories çš„å˜åŒ–
watch(selectedCategories, (newVal) => {
  console.log('Selected categories changed:', newVal);
}, { deep: true });
</script>

<template>
  <div class="sidebar">
    <div class="sidebar-header">
      <h3>åˆ†ç±»</h3>
    </div>
    <ul>
      <li v-for="category in categories" :key="category">
        <button
            @click="toggleCategory(category)"
            :class="{'selected': selectedCategories.includes(category)}">
          {{ category }}
        </button>
        <div :class="{'underline': true, 'selected-underline': selectedCategories.includes(category)}"></div>
      </li>
    </ul>
  </div>
</template>

